/*
 * List of class names that we
 * use throughout the library to
 * manipulate the DOM.
 */

export const MODAL = 'sweet-alert';
export const OVERLAY = 'sweet-overlay';

/* Modifiers */
export const SHOW_MODAL = `${MODAL}--show`;

/* Children */
export const MODAL_TITLE = `${MODAL}__title`;
export const MODAL_TEXT = `${MODAL}__text`;

export const ICON = 'sa-icon';
export const BUTTON = 'swal-button';
export const CONFIRM_BUTTON = `${BUTTON}--confirm`;
export const CANCEL_BUTTON = `${BUTTON}--cancel`;

/*
 * Get a DOM element from a class name:
 */
export const getNode = (className: string) => {
  const selector = `.${className}`;

  return document.querySelector(selector);
};




