import { init } from './modules/init';

import {
  openModal,
  closeModal,
} from './modules/actions';

class swal {

  constructor(name: string) {
    init();

    // For fade animation to work:
    setTimeout(() => {
      openModal();
    });

    /*
    return new Promise((resolve, reject) => {
      return "ler";
    });
     */
  }

  static open() {
    openModal();
  }

  static close() {
    closeModal();
  }
  
};

export default swal;




